import React, { useState, useEffect } from 'react';
import Main from './components/main/Main';
import './App.css'
import MainPage from './pages/MainPage/MainPage';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  Link
} from "react-router-dom";
import PricePage from './pages/PricePage/PricePage';
import FeaturePage from './pages/FeaturPage/FeaturePage';
import ContactPage from './pages/ContactPage/ContactPage';
import ElementPage from './pages/ElementPage/ElementPage';
import BlogePage from './pages/BlogPage/BlogePage';
import BlogeDetailPage from './pages/blogDetailPage/BlogeDetailPage';

function App() {
  const [postList, setPostList ] = useState([])


  useEffect(() => {
    fetch('http://localhost:4000/posts', {
      headers: {
        'Content-Type': 'application/json',
     },
    method: 'GET',

      }).then(respose=>respose.json())
      .then(doc=>{
        if(doc==""){
          return
        }
        doc.map((ele)=>setPostList((pre)=>[...pre, ele]))
        
        console.log(postList);
      })
      .catch(err=>console.log(err))

      
      return ( () => setPostList(()=>[]))
  }, [])
  
  return (
    <Router>
        <Switch>
          <Route path="/feature">
            <FeaturePage />
          </Route>

          <Route path="/price">
             <PricePage /> 
          </Route>

          <Route path="/contact">
            <ContactPage />
          </Route>

          <Route path="/element">
            <ElementPage />
          </Route>

          <Route exact path="/" axect>
          <MainPage />
          </Route>
          {/* <Route path="/blogs">
          <Redirect to="/blogs/" />
          </Route> */}

          <Route path="/blogs/" exact>
          <BlogePage />
          </Route>
        {/* { postList.map((ele)=>{
            return <Route path="/blogs/{slug}">
                  <BlogeDetailPage />
                 </Route>
          })} */}
          <Route path="/blogs/blog/:blogId">
          <BlogeDetailPage />
          </Route>
          <Route path="/blogs/category/:categoty">
          <BlogePage />
          </Route>
          <Route path="/blogs/page/:pageId">
          <BlogePage />
          </Route>
          
          <Route path="/blogdetail" axect>
          <BlogeDetailPage /> 
          </Route>
        </Switch>
    </Router>
  );
}

export default App;
