                            <form target="_blank" action="https://spondonit.us12.list-manage.com/subscribe/post?u=1462626880ade1ac87bd9c93a&amp;id=92a4423d01" method="get" novalidate="true">
                                <div className="input-group">
                                    <input type="email" className="form-control" name="EMAIL" placeholder="Your Email Address" onFocus="this.placeholder = ''" onBlur="this.placeholder = 'Your Email Address '" />
                                    <div className="input-group-append">
                                        <button className="btn click-btn h-100" type="submit">
                                            <i className="fab fa-telegram-plane">
                                            <FaTelegramPlane />
                                            </i>
                                        </button>
                                    </div>
                                </div>
                                <div style={{ position: 'absolute', left: '-5000px' }}>
                                    <input name="b_36c4fd991d266f23781ded980_aefe40901a" tabindex="-1" value="" type="text" />
                                </div>

                                <div className="info"></div>
                            </form>
