import React from 'react'
import './ConSection.css'
import { AiOutlineHome, AiOutlineMail } from 'react-icons/ai'
import { TiDeviceTablet } from 'react-icons/ti'

import { Container, Row, Col, Form, Button } from 'react-bootstrap'
import ContactForm from '../contactForm/ContactForm'

const ConSection = (props) => {
    return (
        <section className='contact-section area-padding'>
            <Container>
                <Row>
                    <div class="d-none d-sm-block mb-5 pb-4">
                        {props.children}
                    </div>

                    <Col md={12}>
                        <h2 className="contact-title">Get in Touch</h2>
                    </Col>
                     
                    <Col lg={8}>
                        <ContactForm />
                    </Col>

                    <Col lg={4} >
                        <div className="media contact-info d-flex">
                            <span className="contact-info__icon"><i className="ti-home"><AiOutlineHome /></i></span>
                            <div className="media-body">
                                <h3>Buttonwood, California.</h3>
                                <p>Rosemead, CA 91770</p>
                            </div>
                        </div>
                        <div className="media contact-info d-flex">
                            <span className="contact-info__icon"><i className="ti-tablet"><TiDeviceTablet /></i></span>
                            <div className="media-body">
                                <h3><a href="tel:454545654">00 (440) 9865 562</a></h3>
                                <p>Mon to Fri 9am to 6pm</p>
                            </div>
                        </div>
                        <div className="media contact-info d-flex">
                            <span className="contact-info__icon"><i className="ti-email"><AiOutlineMail /></i></span>
                            <div className="media-body">
                                <h3><a href="mailto:support@colorlib.com">support@colorlib.com</a></h3>
                                <p>Send us your query anytime!</p>
                            </div>
                        </div>
                    </Col>

                </Row>

            </Container>


        </section >
    )
}

export default ConSection
