import React from 'react'
import { Container, Row, Col ,Form ,Button } from 'react-bootstrap'

const ContactForm = () => {
    return (                   
                    
                        <form className="form-contact contact_form" action="contact_process.php" method="post" id="contactForm" novalidate="novalidate">
                            <Row>
                                <Col md={12}>
                                    <Form.Group className="mb-3" controlId="exampleForm.ControlTextarea1">
                                        <Form.Control as="textarea" rows={10} placeholder="Enter Your Comment" />
                                    </Form.Group>
                                </Col>
                                <Col sm={6}>
                                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                        <Form.Control size="lg" type="text" placeholder="Enter Your Name" />
                                    </Form.Group>

                                </Col>
                                <Col sm={6}>
                                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                        <Form.Control size="lg" type="email" placeholder="enter your email" />
                                    </Form.Group>
                                </Col>
                                <Col md={12}>
                                    <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                        <Form.Control size="lg" type="text" placeholder="Enter Subject" />
                                    </Form.Group>

                                </Col>
                            </Row>
                            <Form.Group className="mt-3" controlId="exampleForm.ControlInput1">
                                <Button variant="danger">SEND MESSAGE</Button>
                            </Form.Group>
                        </form>
    )
}

export default ContactForm
