import React, { useEffect, useState } from 'react'
import { Container, Row, Col, Button } from 'react-bootstrap'
import Moment from 'react-moment';
import slugify from 'react-slugify';
import {
    BrowserRouter as Router,
    Link,
    Switch,
    Route,
    useParams
} from "react-router-dom";
import { HiOutlineSearch } from 'react-icons/hi'
import './BlogSideBar.css'


const BlogSideBar = () => {
    const [categoryList, setCategoryList] = useState([])
    const [postList, setPostList] = useState([])

    const [newBloglist, setNewBlogList] = useState([
        { _id: 1, title: 'From life was you fish...', img: 'post1.jpg', date: 'January 12, 2019' },
        { _id: 2, title: 'The Amazing Hubble...', img: 'post2.jpg', date: '03 Hours ago' },
        { _id: 3, title: 'Astronomy Or Astrology...', img: 'post3.jpg', date: '02 Hours ago' },
        { _id: 4, title: 'Asteroids telescope...', img: 'post4.jpg', date: '01 Hours ago' }
    ])



    useEffect(() => {
        fetch('http://localhost:4000/category/', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },

        }).then(respose => respose.json())
            .then(doc => {
                doc.map((ele) => setCategoryList((pre) => [...pre, ele]))
                console.log(categoryList)
            })
            .catch(err => console.log(err))
        return (() => setCategoryList(() => []))

    }, [])
    useEffect(() => {
        fetch('http://localhost:4000/posts', {
            headers: {
                'Content-Type': 'application/json',
            },
            method: 'GET',

        }).then(respose => respose.json())
            .then(doc => {
                doc.map((ele) => setPostList((pre) => [...pre, ele]))

                console.log(postList);
            })
            .catch(err => console.log(err))


        return () => setPostList(() => [])

    }, [])

    return (
        <Col lg={4} >

            <div className="blog_right_sidebar">
                <aside className="single_sidebar_widget search_widget">
                    <form action="#">
                        <div className="form-group">
                            <div className="input-group mb-3">
                                <input type="text" className="form-control" placeholder="Search Keyword" />
                                <div className="input-group-append">
                                    <Button><i className="ti-search"><HiOutlineSearch /></i></Button>
                                </div>
                            </div>
                        </div>
                        <Button className="button rounded-0 primary-bg text-white w-100" type="submit">Search</Button>
                    </form>
                </aside>

                <aside className="single_sidebar_widget post_category_widget">
                    <h4 className="widget_title">Category</h4>
                    <ul className="list cat-list">
                        {
                            categoryList.map((ele,i) => {
                                const filt = postList.filter((el,i) => el.category.includes(ele.categoryTitle))
                                return (
                                    <li kry={ele._id}>
                                        <Link to={{ pathname: `/blogs/category/${slugify(ele.categoryTitle)}/`, catId: `${ele.categoryTitle}` }} className="d-flex">
                                            <p>{ele.categoryTitle}</p>
                                            <p>{` (${filt.length})`}</p>
                                        </Link>
                                    </li>
                                )
                            }
                            )
                        }

                    </ul>
                </aside>

                <aside className="single_sidebar_widget popular_post_widget">
                    <h3 className="widget_title">Recent Post</h3>
                    {postList.slice(Math.max(postList.length - 4, 0)).map((ele) => {
                        return (
                            <div key={ele._id} className="media post_item d-flex">
                                <img src={`http://localhost:4000/images/post/${ele.image[0]}`} className={'w-75 h-75 flex-fill'} alt="post" />
                                <div className="media-body">
                                    <Link to={{ pathname: `/blogs/blog/${ele.slug}/`, blogId: `${ele._id}` }}>
                                        <h3>{ele.title}</h3>
                                    </Link>
                                    <p><Moment fromNow>{ele.created}</Moment></p>
                                    
                                </div>
                            </div>
                        )
                    })}


                </aside>
                <aside className="single_sidebar_widget tag_cloud_widget">
                    <h4 className="widget_title">Tag Clouds</h4>
                    <ul className="list">
                        <li>
                            <a href="#">project</a>
                        </li>
                        <li>
                            <a href="#">love</a>
                        </li>
                        <li>
                            <a href="#">technology</a>
                        </li>
                        <li>
                            <a href="#">travel</a>
                        </li>
                        <li>
                            <a href="#">restaurant</a>
                        </li>
                        <li>
                            <a href="#">life style</a>
                        </li>
                        <li>
                            <a href="#">design</a>
                        </li>
                        <li>
                            <a href="#">illustration</a>
                        </li>
                    </ul>
                </aside>


                <aside className="single_sidebar_widget instagram_feeds">
                    <h4 className="widget_title">Instagram Feeds</h4>
                    <ul className="instagram_row flex-wrap">
                        {postList.slice(Math.max(postList.length - 5, 0)).map((ele) => {
                            return (
                                <li key={ele._id}>
                                    <Link href={{ pathname: `/blogs/${ele.slug}/`, blogId: `${ele._id}` }}>
                                        <img className="img-fluid" src={`http://localhost:4000/images/post/${ele.image[0]}`} alt="" />
                                    </Link>
                                </li>

                            )
                        })
                        }

                        <li>
                            <a href="#">
                                <img className="img-fluid" src="img/instagram/widget-i2.png" alt="" />
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img className="img-fluid" src="img/instagram/widget-i3.png" alt="" />
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img className="img-fluid" src="img/instagram/widget-i4.png" alt="" />
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img className="img-fluid" src="img/instagram/widget-i5.png" alt="" />
                            </a>
                        </li>
                        <li>
                            <a href="#">
                                <img className="img-fluid" src="img/instagram/widget-i6.png" alt="" />
                            </a>
                        </li>
                    </ul>
                </aside>


                <aside className="single_sidebar_widget newsletter_widget">
                    <h4 className="widget_title">Newsletter</h4>

                    <form action="#">
                        <div className="form-group mb-3">
                            <input type="email" className="form-control" placeholder="Enter email" required="" />
                        </div>
                        <button className="button rounded-0 primary-bg text-white w-100" type="submit">Subscribe</button>
                    </form>
                </aside>
            </div>


        </Col>
    )
}

export default BlogSideBar
