import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';

const AnyReactComponent = ({ text }) => <div>{text}</div>;

class SimpleMap extends Component {
  static defaultProps = {
    center: {
      lat: 26.91,
      lng: 75.78
    },
    zoom: 2
  };

  render() {
    return (
      // Important! Always set the container height explicitly
      <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3558.3095339450488!2d75.74382971445448!3d26.89366986738524!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x396db49309bb5e51%3A0x959cd446af222d7d!2sInfonic%20Training%20%26%20Development%20Center%20-%20Digital%20Marketing%20Course!5e0!3m2!1sen!2ssg!4v1628228238832!5m2!1sen!2ssg" width="100%" height="450" style={{ border:'0' }} allowfullscreen="" loading="lazy"></iframe>
    );
  }
}

export default SimpleMap;