import React, {useState, useEffect } from 'react'


import { Container, Row, Col, Button,Pagination, PageItem } from 'react-bootstrap'
import Moment from 'react-moment';
import {
    BrowserRouter as Router,
    Link,
    Switch,
    Route,
    useParams,
    useLocation,
    useHistory
  } from "react-router-dom";
import './BlogMain.css'
import { HiOutlineSearch } from 'react-icons/hi'
import { FaAngleLeft, FaAngleRight, FaRegComments, FaRegUser } from 'react-icons/fa'
import BlogSideBar from '../blogSideBar/BlogSideBar'

const BlogMain = () => {
    let param = useParams();
    const { catId, pathname } = useLocation()
    let history = useHistory();
  const [catIdName, setCatIdName] = useState(catId)
  const [postList, setPostList ] = useState([])
  const [pegItem, setPegItem] = useState([])
  const [pagData, setPagData] = useState({
    diff:10, active: param.pageId ?? 1,
  })
  console.log(param.pageId);
  let { active } = pagData;
  console.log(catId)
  if(catId != catIdName){
    setCatIdName(()=>{
      return catId
    })
    setPagData((pre)=>{
     return{ ...pre,active:1}

    })
  }



// useEffect(() => {
//     for (let number = 1; number <= Math.ceil((postList.length) / pagData.diff); number++) {
//         pegItem.push(
//           <Pagination.Item key={number} active={number === pagData.active} onClick={(current)=>pegHandler(current.target.text) } value={number}>
//             {number}
//           </Pagination.Item>,
//         );
//       }
          
// },[])
  
const checkPath = (i) => {
  if(/\d/.test(pathname)){
      return (`${pathname.replace(/\d+/g,'')}`);
    }else{
      return (`${pathname}page/`)
    }
console.log()
 } 
const pegHandler =(act)=>{
    if(act == undefined) return
    setPagData((pre)=>{
        return{
            ...pre,
            active:act,
        }
    })
    history.push(`${checkPath()}${act}`);
}
console.log(pagData)

useEffect(() => {
  fetch(`http://localhost:4000/posts/filter/category?page=${pagData.active - 1}&row=${pagData.diff}&category=${catId || ''}`, {
    headers: {
      'Content-Type': 'application/json',
   },
  method: 'GET',

    }).then(respose=>respose.json())
    .then(doc=>{
      (doc.getPost).map((ele)=>setPostList((pre)=>[...pre, ele]))
      console.log(postList);
        for(let i = 1; i <= Math.ceil(doc.total / pagData.diff); i++ ){
          setPegItem((pre)=>{
            return [...pre , i]
          }
        )
      }
    }
    )
    .catch(err=>console.log(err))

    
    return () => {
      setPostList(()=>[])
      setPegItem(()=>[])
    } 
    
}, [catId, active])



const preNex = (ele) => {

    if(ele == 'pre'){
      if(pagData.active <= 1 ){
        return
      }else{
        history.push(`${checkPath(pagData.active)+(parseInt(pagData.active) - 1)}`) 
    
      return(
        setPagData((pre)=>{
          return{ ...pre ,active: parseInt(pagData.active) - 1} 
        })
      )
    }
  }else{
    if(pagData.active >= pegItem.length ){
      return
    }else{
      history.push(`${checkPath(pagData.active)}${(parseInt(pagData.active) + 1)}`) 
      return(
        setPagData((pre)=>{
          return{ ...pre ,active: parseInt(pagData.active) + 1} 
        })
      )
    }
  }
}


    // useEffect(() => {
    //     fetch('http://localhost:4000/posts', {
    //       headers: {
    //         'Content-Type': 'application/json',
    //      },
    //     method: 'GET',
    
    //       }).then(respose=>respose.json())
    //       .then(doc=>{
    //         if(doc==""){
    //           return
    //         }
    //         if(catId !== undefined){
    //             console.log('yoe data is not empty')
    //         const filt = doc.filter((ele,i)=>ele.category.includes(catId))
    //         filt.map((ele)=>setPostList((pre)=>[...pre, ele]))
    //         }else{
    //         doc.map((ele)=>setPostList((pre)=>[...pre, ele]))
    //         }

    //         for(let num = 1; num <= Math.ceil((postList.length) / pagData.diff); num++ ){
    //             setPegItem((pre)=>[ ...pre, num])
    //         }
            
    //         console.log(postList);
    //       })
    //       .catch(err=>console.log(err))

          
    //       return () => setPostList(()=>[])
          
    //   }, [catId])

      useEffect(()=>{
        return(()=>setPegItem(()=>[]))
      },[catId])


    return (
        <section className='blog_area area-padding'>
            <Container>
                <Row>
                    <Col lg={8} className='mb-5 me-lg-0'>

                        <div className="blog_left_sidebar">

                            {postList.map((ele)=>{
                                const par = ele.paragraph;
                                const para= (par).substr(((par).indexOf('>') + 1),((par).indexOf('</p>') - 6));
                                return(
                            <article key={ele._id} className="blog_item">
                                <div className="blog_item_img">
                                    <img className="card-img rounded-0" src={`http://localhost:4000/images/post/${ele.image[0]}`} alt="" />
                                    <Link to={{pathname:`/blogs/blog/${ele.slug}/`,blogId:`${ele._id}`}} className="blog_item_date">
                                        <h3><Moment format="DD">
                                        {ele.modified}
                                          </Moment></h3>
                                        <p><Moment format="MMM">
                                        {ele.modified}
                                          </Moment></p>
                                    </Link>
                                </div>
                                <div className="blog_details">
                                    <Link to={{pathname:`/blogs/blog/${ele.slug}/`,blogId:`${ele._id}`}} className="d-inline-block" >
                                    <h2>{ele.title }</h2>
                                    </Link>
                                    {/* <h3>{para}</h3> */}
                                    <p>{para.length >= 175 ? `${para.substring(0,175)} ...` : para}</p>
                                    <ul className="blog-info-link">
                                        <li><a href="#"><i className="far fa-user"><FaRegUser /></i>{(ele.category).join(', ')}</a></li>
                                        <li><a href="#"><i className="far fa-comments"><FaRegComments /></i> 03 Comments</a></li>
                                    </ul>
                                </div>
                            </article>)

                            })}


                            <nav className="blog-pagination justify-content-center d-flex">
                            <Pagination size="lg">
                                  
                                  <Pagination.Prev onClick={()=>preNex(`pre`)} />
                                  {/* {pagData} */}
                                  {pegItem.map((ele,i)=>{

                                    
                                      return (
                                        // <Link to={`${checkPath(i+1)}`}>

                                          <Pagination.Item activeLabel key={i} active={i === pagData.active - 1} onClick={(current)=>pegHandler(current.target.text)} value={ele}>
                                            {ele}
                                          </Pagination.Item>
                                          // </Link>

                                      )
                                  })}
                                 
                                  <Pagination.Next onClick={()=>preNex(`nex`)} />
                                  
                            </Pagination>
                               
                            </nav>
                            
                        </div>


                    </Col>

                    <BlogSideBar />

                </Row>
            </Container>

        </section>
    )
}


export default BlogMain
