import React from 'react'
import { Container, Row, Nav, Breadcrumb } from 'react-bootstrap'


const TitleCard = (props) => {
    return (
    <section className='hero-banner d-flex align-items-center'>
        <Container className='text-center'>
            <h2>{props.title}</h2>
            <Breadcrumb as={'nav'} className='banner-breadcrumb'>
                <Breadcrumb.Item href="#">Home</Breadcrumb.Item>
                <Breadcrumb.Item href="#">{props.title}</Breadcrumb.Item>
            </Breadcrumb>

        </Container>


    </section>
    )
}

export default TitleCard
