import React from 'react'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import './Caruosel.css';

const Caruosel = () => {
    const settings = {
        infinite: true,
        autoplay: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 1,
        arrows : false,
    };
    const caruoselImages = [
            '1.png','2.png','3.png','4.png','5.png','6.png',
        ];

    return (
            <Slider {...settings}>
                {caruoselImages.map((img, ind) => (
                <div key={ind} className="single-brand-item d-table text-center">
                       <div className="d-table-cell">
                           <img src={`../../../../img/logo/${img}`} alt="" />
                       </div>
                </div>
                ))}
                    
            </Slider>

    )
}

export default Caruosel
