import React from 'react'
import { Col } from 'react-bootstrap'
import { AiOutlineHome } from 'react-icons/ai'
import { RiShoppingBagLine } from 'react-icons/ri'
import './DescList'



const DescList = (props) => {
    const descList = [
        {
            title: 'Standerd',
            price: '20',
            icon: 'AiOutlineHome',
            list: ['2Gb bandwidth', 'two Acount', 'two Acount', '15GB Storage', '3 Host Domain', '24/7 Support']
        },
        {
            title: 'Standerd',
            icon: 'RiShoppingBagLine',
            price: '20',
            list: ['2Gb bandwidth', 'two Acount', 'two Acount', '15GB Storage', '3 Host Domain', '24/7 Support']
        },
        {
            title: 'Standerd',
            price: '20',
            icon: 'AiOutlineHome',
            list: ['2Gb bandwidth', 'two Acount', 'two Acount', '15GB Storage', '3 Host Domain', '24/7 Support']
        }
    ]
    return (

        descList.map((item, index) => (
            <Col sm={6} lg={3} key={index}>

                <div className="single-pricing">
                    <div className="pricing-icon">
                        <i className="ti-home">{
                             <AiOutlineHome />
                        // (item.icon == 'RiShoppingBagLine' && <RiShoppingBagLine />)
                        }</i>
                    </div>
                    <div className="single-pricing-content">
                        <h5>{item.title}</h5>
                        <h4>${item.price}<span className="currency_line">/</span><span>month</span></h4>
                        <ul>
                            {descList[index].list.map((list, ind) => (
                                <li key={ind}>{list}</li>
                            ))}
                        </ul>
                        <a href="#">Purchase Now</a>
                    </div>
                </div>
            </Col>
        ))


    )
}

export default DescList
