import React from 'react'
import './Section2.css'
import { Container, Row, Col, Button } from 'react-bootstrap'
import SecCol from './sec-col/SecCol'


const Section2 = (props) => {
    return (
        <section className={`${props.className} service-area area-padding`}>
            <Container>
                 <Row>
                     <SecCol />
                     
                 </Row>
            </Container>
        </section>
    )
}

export default Section2
